import React from 'react';
import './Interface.css';
import Page from './Page/Page.js';
import Control from './Controls/Control.js';
import Data from '../Data/Data.js';

class Interface extends Control {
  
	constructor(props){
		super(props);
		Interface.instance = this;
	}
	
	static getImageUrl(fileName){ return process.env.PUBLIC_URL + "/images/" + fileName; }
	
	static setTitle(subtitle){ 
		document.getElementById("title").innerHTML = 
			Data.instance.getTitle() + (subtitle?" - " + subtitle:"") ; 
	}
	
	render(){
		return (
			<Page />
		);
	}
 
	onAfterRender(){
		Interface.setTitle("");
	}
}

export default Interface;
