import React from 'react';
import Interface from '../../Interface.js';
import './Panel.css';
import Control from '../../Controls/Control.js';
import Data from '../../../Data/Data.js';

class Panel extends Control {

	constructor(props){
		super(props);
		this.state = {
			panelBackground: {
				backgroundImage:"url(" + Interface.getImageUrl("parrot-2909834_1280.jpg") + ")"
			}
		}
		Panel.instance = this;
	}
	
	render(){
		return (
			<div class="panelPositioner initialPanel">
				<div class="panelContainer musicPanel" style={this.state.panelBackground}>
					<div class="panelText">
						<div class="panelHeading">
							<span id="musicPanelTitle" class="panelTitle"></span><br/><br/>
							<span id="musicPanelSubtitle" class="panelSubtitle"></span>
						</div>
						<br/>
						<br/>
						<span id="musicPanelParagraph" class="panelParagraph"></span>
					</div>
				</div>
			</div>
		);
	}
	
	onAfterDataChange(){
		let p = Data.instance.getCurrPanel();
		document.getElementById("musicPanelTitle").innerHTML = p.title;
		document.getElementById("musicPanelSubtitle").innerHTML = p.subtitle;
		document.getElementById("musicPanelParagraph").innerHTML = p.text;
	}
}

export default Panel;
