import React from 'react';
import store from './Imp/Store.js';

class Data extends React.Component {
  
	constructor(props){
		super(props);
		Data.instance = this;
	}
	
	register(callback){ return store.subscribe(callback); }
	
	getTitle(){ 
		if( undefined === store.getState().title ){ store.dispatch({type:"GET_TITLE"}); }
		return store.getState().title;
	}
	
	getCurrPanel(){
		if(undefined === store.getState().panel){ store.dispatch({type:"GET_CURR_PANEL"}); }
		return store.getState().panel;
	}
	
	getPrevPanel(){
		store.dispatch({type:"GET_PREV_PANEL"});
		return store.getState().panel;
	}
	
	getNextPanel(){
		store.dispatch({type:"GET_NEXT_PANEL"});
		return store.getState().panel;
	}
	
	render(){ return null; }
}

export default Data;
