import {createStore} from 'redux';

function title(state, action){
	if("GET_TITLE" === action.type){
		return "Alfred Carn";
	}
	else return state;
}

function panel(state, action){
	let count = 4;
	let curr = undefined === state ? 0 : state.curr ;
	if("GET_CURR_PANEL" === action.type){
		return getPanel(count, curr);
	}
	else if("GET_PREV_PANEL" === action.type){
		curr = (0<curr && curr<count) ? curr-1 : curr ;
		return getPanel(count, curr);
	}
	else if("GET_NEXT_PANEL" === action.type){
		curr = (0<=curr && curr<(count-1)) ? curr+1 : curr ;
		return getPanel(count, curr);
	}
	else return state;
}

function getPanel(count, index){
	if(0===index){
		return {
			count: count,
			curr: index,
			title: "Detached",
			subtitle: "by Alfred Carn",
			text: `
						<div style="position:relative;left:300px;"><i>May 8, 2020</i></div>
				<br/>	<img align="left" style="margin-right:100px;width:200px;height:200px;" src="images/Detached.png"/>
						<i>Detached</i> is composed in six parts, performed and recorded by Alfred.
						<ul style="margin-top:0px;margin-left:20px;margin-bottom:0px;">
							<li>Two cellos</li>
							<li>Two flutes</li>
							<li>Two clarinets</li>
							<li>Harp</li>
							<li>Sampled piano</li>
						</ul>
						You can find it
						<a style="color:#ff8888;" target="_blank" href="https://www.youtube.com/watch?v=RIgdhciWzUc">here</a> on YouTube.
						I hope you like it!
				<br/>
			`
		}
	}
	else if(1===index){
		return {
			count: count,
			curr: index,
			title: "The Composing/Recording Process:",
			subtitle: "A labour of love",
			text: `
						<img align="left" style="margin-right:50px;width:152px;height:270px;" src="images/cello3.jpg"/>
						Being an ear player I never learned how to properly read music. 
						Later in life I wanted to compose classical music, 
						stop using keyboard samples and try to play the instruments myself.
				<br/>
				<br/>	As a result I developed a particular way of working.												
						So instead of first composing the entire piece of music, I compose and record together in one process.
				<br/>	
				<br/>	This alleviates the need to keep lots of notes in my head.
			`
		}
	}
	else if(2===index){
		return {
			count: count,
			curr: index,
			title: "The Composing/Recording Process:",
			subtitle: "A labour of love",
			text: `
						<img align="left" style="margin-right:50px;width:152px;height:270px;" src="images/cello3.jpg"/>
						Typically I'll compose a small phrase for a particular instrument.
						I'll record successive takes, adjusting small details.
						For example I may vary the vibrato speed, play softer, crescendo later, or try different notes.
				<br/>	
				<br/>	Eventually it'll feel right, then it's on to the next phrase and so on until the piece is finished.
				<br/>
				<br/>	The microphone signal is recorded dry without any EQ or signal processing.
						After tracking and a light mixdown I'll apply a general reverb on the finished mix before mastering.
			`
		}
	}
	else if(3===index){
		return {
			count: count,
			curr: index,
			title: "About",
			subtitle: "Thanks for visiting!",
			text: `
				<br/>
						<img align="left" style="margin-right:87px;width:275px;height:220px;" src="images/photo.jpg"/>
				<br/>	
				<br/>	Alfred is a computer programmer by day. This site was with created with 
						<a style="color:#ff8888;" target="_blank" href="http://alfredcarn.ca/musicangular">Angular</a>
						and 
						<a style="color:#ff8888;" target="_blank" href="http://alfredcarn.ca/musicreact">React/Redux</a> 
						with HTML5/CSS.
				<br/>	
				<br/>	Stay tuned for new music in the works!
			`
		}
	}
}

function storeReducers(store, action){
	store = ( undefined === store ) ? {} : store ;
	return {
		title: title(store.title, action),
		panel: panel(store.panel, action)
	};
}

export default createStore(storeReducers);
