import React from 'react';
import Data from '../../Data/Data.js'

class Control extends React.Component{
	constructor(props){
		super(props);
		Data.instance.register(() => this.fireCallbacks());
	}
	
	fireCallbacks(){
		if(this.onBeforeDataChange){ this.onBeforeDataChange(); }
		this.forceUpdate(); // This forces the control to render again.
		if(this.onAfterDataChange){ setTimeout(()=> this.onAfterDataChange(), 1); } 
	}
}

export default Control;