import React from 'react';
import './Page.css';
import Background from './Background/Background.js';
import Logo from './Logo/Logo.js';
import Panel from './Panel/Panel.js';
import NextPrevButtons from '../Controls/NextPrevButtons/NextPrevButtons.js';
import Control from '../Controls/Control.js';
import Data from '../../Data/Data.js';
import Interface from '../Interface.js';

class Page extends Control {
	
	constructor(props){
		super(props);
		Page.instance = this;
	}
	
	render(){
		return (
			<>
				<Background/>
				<Logo/>
				<Panel/>
				<NextPrevButtons onCallback={this}/>
			</>
		);
	}
	
	// Changing the data automatically refreshes the panel.
	onAfterDataChange(){ Interface.setTitle(Data.instance.getCurrPanel().title); }
	prevOnClick(){ Data.instance.getPrevPanel(); }
	nextOnClick(){ Data.instance.getNextPanel(); }
}

export default Page;
