import React from 'react';
//import Interface from '../../Interface.js';
import './NextPrevButtons.css';
import Control from '../Control.js';
//import Data from '../../../Data/Data.js';
//import Page from '../../Page/Page.js';

class NextPrevButtons extends Control {
	
	render(){
		return (
			<div class="navigatorPositioner">
				<div id="navigatorPrev" class="navigatorButton navigatorPrev" onClick={()=>this.prevOnClick()}>
					<div class="navigatorArrow">{"<"}</div>
				</div>
				<div id="navigatorNext" class="navigatorButton navigatorNext" onClick={()=>this.nextOnClick()}>
					<div class="navigatorArrow">{">"}</div>
				</div>
			</div>
		);
	}
	
	buttonOnClick(b){
	  b.style.animationName = "navigatorClickSequence";
	  b.style.animationDuration = "0.5s";
	  b.style.animationFillMode = "both";
	  setTimeout( () => {b.style.animationName = "";}, 600);
	}
	
	prevOnClick(){
		var b = document.getElementById("navigatorPrev");
		this.buttonOnClick(b);
		if(undefined !== this.props.prevOnClick) {this.props.prevOnClick();}
		else if(undefined !== this.props.onCallback){
			if(undefined !== this.props.onCallback.prevOnClick){this.props.onCallback.prevOnClick();}
		}
	}
	
	nextOnClick(){
		var b = document.getElementById("navigatorNext");
		this.buttonOnClick(b);
		if(undefined !== this.props.nextOnClick) {this.props.nextOnClick();}
		else if(undefined !== this.props.onCallback){
			if(undefined !== this.props.onCallback.nextOnClick){this.props.onCallback.nextOnClick();}
		}
	}
}

export default NextPrevButtons;
